
import scroll from "../../../components/scrollAll";
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import dayuan from "../../../common/js/dayuan.js";
import CompDia from "./compDia/template";
import UserSelect from "../../../components/User/template";
import dayjs from "dayjs";
import noData from "../../../components/noData";
import yiwc from "../../../assets/yiwc.png";
import weiwc from "../../../assets/weiwc.png";
import yiqx from "../../../assets/qux.png";
import zhuanj from "../../../assets/zhuanj.png";
import { mapState } from "vuex";
export default {
directives: {
TransferDom
},
data() {
return {
workList: [],
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
pageindex: 1,
loadingShow: false,
putWoText: ["已完成", "未完成", "取消任务", "转其他人"],
putParam: {},
compShow: false,
userBool: false,
defaultKey: [
"TaskResult",
"TaskState",
"TaskPriority",
"TaskContent",
"BeginDate",
"LinkUserID",
"CustomerID",
"TaskType",
"ID"
],
defaltParam: {},
workInfo: {},
defaultExt: [],
iconsArr: [yiwc, weiwc, yiqx, zhuanj]
};
},
computed: {
...mapState(["CurrentUserID"]),
extName() {
let name = this.workList.map(val => val.Executors);
return name;
},
aImg() {
let arr = this.workList.map(val => {
return dayuan.aImg(val.CreateUserID);
});
return arr;
},
aImg2() {
let arr = this.workList.map(val => {
return dayuan.aImg2(val.CreateUserID);
});
return arr;
},
exeImg() {
let exelist = this.workList.map(val => val.Executors);
let arr = [];
exelist.forEach((item, key) => {
if (!arr[key]) {
arr[key] = [];
}
item.forEach(items => {
arr[key].push(dayuan.aImg(items.UserID));
});
});
return arr;
},
createTime() {
let timeArr = this.workList.map(val =>
dayjs(val.BeginDate).format("YYYY-MM-DD HH:mm")
);

return timeArr;
},
neWoSt() {
return dayuan.newTasState(this.param.taskState);
},
neWoClass() {
return dayuan.newTasStateClass(this.param.taskState);
},
disabCheck() {
return this.defaultExt.map(val => val.UserID);
}
},
props: {
ovoState: {
type: Number
},
param: {
type: Object
},
states: {
type: Number
}
},
watch: {
ovoState(newVal) {
this.$refs["scroll"].scrollTo(0, 0, 200);
this.pageindex = 1;
this.getList(this.param, 1);
},
param(newParam) {
this.$refs["scroll"].scrollTo(0, 0, 200);
this.pageindex = 1;
this.getList(this.param, 1);
},
workList() {
setTimeout(() => {
this.$refs.scroll.forceUpdate(true);
}, 500);
}
},
components: {
scroll,
Loading,
CompDia,
UserSelect,
noData
},
methods: {
getList(param, index) {
this.$vux.loading.show({
text: "加载中"
});
let obj = {
0: `/CrmTask/GetExecMyCrmTaskList`,
1: `/CrmTask/GetMyCrmTaskList`,
2: `/CrmTask/GetSendMyCrmTaskList`
};
const _param = Object.assign({}, param, {
page: index,
count: 6
});
return this.getAxios(`${obj[this.ovoState]}`, _param).then(res => {
if (res.Data.List.length > 0) {
if (index === 1) {
this.workList = res.Data.List;
} else {
this.workList = [...this.workList, ...res.Data.List];
}
} else if (res.Data.List.length <= 0 && index === 1) {
this.workList = [];
}
this.$refs.scroll.forceUpdate();
setTimeout(() => {
this.$vux.loading.hide();
}, 500);
});
},
async listPullup() {
this.pageindex++;
let pullBool = await this.getList(this.param, this.pageindex);
if (this.workList.length === 0) {
return false;
}
this.$refs.scroll.scroll.finishPullUp();
this.$refs.scroll.refresh();
},
listPulldown() {
this.pageindex = 1;
this.getList(this.param, 1);
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.refresh();
},
putworkSta(item, txt) {
let state = dayuan.putWorkState(txt);
this.putParam = {
id: item.ID,
taskState: state,
taskResult: ""
};

if (state === 1 || state === 4) {
this.compShow = true;

} else if (state === 3) {
if (item.TaskType === 2) {

this.compShow = true;
} else {

this.$router.push({
path: "/addFollow",
query: {
cId: item.CustomerID,
cName: item.CustomerName,
cLink: item.LinkUserID,
cState: item.CustState
}
});
}
this.completeWork();
} else {
this.transferWork(item.ID);
}
},
async cancelWork(param) {
let put = await this.putWorkMeth(param); //先修改状态
await this.getList(this.param, 1);
},
completeWork() {},
async transferWork(id) {
let info = await this.getWorkInfo(id); //先获取任务详情

this.workInfo = {};
this.defaultKey.forEach(key => {
this.workInfo[key] = info[key];
});
this.workInfo["Senders"] = info["Senders"].map(val => {
return {
UserID: val.UserID,
IsDelete: 0,
ID: val.ID
};
});
this.defaultExt = info["Executors"].map(val => {
return {
UserID: val.UserID,
IsDelete: 1,
ID: val.ID
};
});
this.userBool = true;
},
putWorkMeth(param) {
return this.Axios.post(
`/CrmTask/SetCrmTaskState/${param.id}?taskState=${
param.taskState
}&taskResult=${param.taskResult}`
).then(res => {
this.$vux.toast.show({
text: "成功修改任务状态"
});
});
},
getTxtval(val) {
this.putParam.taskResult = val;
this.cancelWork(this.putParam);
},
getWorkInfo(id) {
return this.Axios.get(`/CrmTask/GetCrmTaskInfoByID/${id}`)
.then(res => {
return res.data.Data;
})
.catch(err => {
console.log(err);
});
},
async saveUser(newUser) {
if (newUser.length) {
this.userBool = false;
let c = newUser.map(val => {
return {
IsDelete: 0,
ID: 0,
UserID: val.ID
};
});
this.workInfo["Executors"] = [...c, ...this.defaultExt];
let savTas = await this.saveTask(c);

await this.getList(this.param, 1);
this.$vux.toast.show({
text: "转交成功"
});
} else {
this.$vux.toast.show({
type: "warn",
text: "请选择新的执行人"
});
}
},

saveTask(li) {
return this.Axios.post(`/CrmTask/SaveCrmTask`, this.workInfo).then(
res => {
if (res.State === 1) {
this.$vux.toast.show({
text: "转交成功"
});
}
let idl = li.map(val => val.UserID);
idl.forEach((val, index) => {
if (val === this.CurrentUserID) {
idl.splice(index, 1);
}
});
this.send(idl, res.data.Data);
}
);
},
send(userID, dataID) {
let msg = {
SendUsers: userID,
EvName: "crm-task",
ImgPath: `${down}/mdtcrm/CRMTask.jpg`,
Title: `您有一条新的待办任务`,
Content: `${this.workInfo.TaskContent}`,
Params: [dataID]
};
this.Axios.post(`${api50}/api/v1/Common/SendSingleEventMsg`, msg)
.then(res => {})
.catch(err => {});
},
init() {
let hei = document.body.clientHeight;
this.$refs.clientList.style.height = `${hei - 77}px`;
},
addFlMeth() {

return new Promise((resolve, reject) => {
this.$bus.on("onSave", data => {
this.cancelWork(this.putParam);
resolve(1);
this.$bus.off("onSave");
});
});
},
toTask(id) {
this.$router.push({ path: "/TaskInfo", query: { id } });
}
},
mounted() {
this.init();
this.getList(this.param, 1);
this.addFlMeth().then(res => {
this.getList(this.param, 1);
});
},
activated() {
this.getList(this.param, 1);
this.addFlMeth().then(res => {
this.getList(this.param, 1);
});
}
};



