
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
data() {
return {
show: null,
txtVal: ""
};
},
props: {
showBo: {
type: Boolean
},
title: {
type: String
}
},
watch: {
showBo(newB) {
this.show = newB;
}
},
components: {
Confirm
},
methods: {
onCancel() {
this.$emit("close");
this.txtVal = "";
},
onConfirm() {
this.$emit("close");
this.$emit("sub", this.txtVal);
this.txtVal = "";
},
onShow() {}
}
};



