import dayuan from "../../../common/js/dayuan.js";
import Lists from "../CaleList/template";
import dayjs from "dayjs";
import CompDia from "../List/compDia/template";
import UserSelect from "../../../components/User/template";
export default {
  data() {
    return {
      fcEvent: [],
      fcCount: [],
      workList: [],
      pageindex: 1,
      beginDates: "",
      endDates: "",
      marks: {},
      crulist: {},
      putParam: {},
      compShow: false,
      userBool: false,
      defaultExt: [],
      workInfo: [],
      defaultKey: [
        "TaskResult",
        "TaskState",
        "TaskPriority",
        "TaskContent",
        "BeginDate",
        "LinkUserID",
        "CustomerID",
        "TaskType",
        "ID"
      ]
    };
  },
  props: {
    ovoState: {
      type: Number
    },
    param: {
      type: Object
    },
    states: {
      type: Number
    }
  },
  computed: {
    dayE() {
      return dayuan.workDate(this.ovoState);
    },
    watchParam() {
      let ram = Object.assign({}, this.param);
      // this.beginDates=ram.beginDate
      // this.endDates=ram.endDate
      ram.beginDate = this.beginDates;
      ram.endDate = this.endDates;
      return ram;
    },
    _param() {
      let ram = Object.assign({}, this.param, {
        page: this.pageindex,
        count: 6
      });
      // this.beginDates=ram.beginDate
      // this.endDates=ram.endDate
      ram.beginDate = this.beginDates;
      ram.endDate = this.endDates;
      return ram;
    },
    newTasStateBack() {
      return dayuan.newTasStateBack(this.param.taskState);
    },
    disabCheck() {
      return this.defaultExt.map(val => val.UserID);
    }
  },
  watch: {
    ovoState(newVal) {
      this.getDates();
      this.pageindex = 1;
    },
    param(newParam) {
      this.getDates();
      this.pageindex = 1;
      this.caleCalss();
    },
    marks(newJson) {
      this.refashDom();
    }
  },
  mounted() {
    this.addFlMeth();
    this.getDates();
    this.init();
    this.pageindex = 1;
  },
  activated() {
    this.getDates();
    this.refashDom();
    this.pageindex = 1;
  },
  components: {
    Lists,
    CompDia,
    UserSelect
  },
  methods: {
    init() {
      this.beginDates = dayjs().format(`YYYY-MM-DD 00:00:00`);
      this.endDates = dayjs().format(`YYYY-MM-DD 23:59:59`);
    },
    refashDom() {
      this.init();
      let box = document.getElementById("cal-box");
      let child = document.getElementById("calBox");
      box.removeChild(child);
      let newBox = document.createElement("div");
      newBox.setAttribute("id", "calBox");
      newBox.style.background = "#fff";
      newBox.style.textAlign = " center";
      box.appendChild(newBox);
      this.xuanr();
      //    异步
      this.caleCalss();
      this.getDeliList(this._param, 1);
    },
    getList(param) {
      return this.getAxios(`/Statistics/GetTaskGroupByStatistics`, param).then(
        res => {
          this.fcEvent = res.Data.map(val => val.Date);
          this.fcCount = res.Data.map(val => val.Count);
        }
      );
    },
    async listPullup() {
      this.pageindex++;
      let pullBool = await this.getDeliList(this._param, this.pageindex);
      if (this.workList.length === 0) {
        return false;
      }
      this.$refs["lists"].$refs.scroll.scroll.finishPullUp();
      this.$refs["lists"].$refs.scroll.refresh();
    },
    listPulldown() {
      this.pageindex = 1;
      this.getDeliList(this._param, this.pageindex);
      this.$refs["lists"].$refs.scroll.scroll.finishPullDown();
      this.$refs["lists"].$refs.scroll.refresh();
    },
    getDeliList(param, index) {
      this.$vux.loading.show({
        text: "加载中"
      });
      let obj = {
        0: `/CrmTask/GetExecMyCrmTaskList`,
        1: `/CrmTask/GetMyCrmTaskList`,
        2: `/CrmTask/GetSendMyCrmTaskList`
      };
      return this.getAxios(`${obj[this.ovoState]}`, param).then(res => {
        if (res.Data.List.length > 0) {
          if (index === 1) {
            this.workList = res.Data.List;
          } else {
            this.workList = [...this.workList, ...res.Data.List];
          }
        } else if (res.Data.List.length <= 0 && index === 1) {
          this.workList = [];
        }
        setTimeout(() => {
          this.$vux.loading.hide();
          this.$refs["lists"].$refs.scroll.forceUpdate();
        }, 500);
      });
    },
    xuanr() {
      let month = (new Date().getMonth() + 1).toString(),
        self = this;

      setTimeout(() => {
        laydate.render({
          elem: "#calBox",
          position: "static",
          calendar: true,
          mark: self.marks,
          showBottom: false,
          theme: "grid",
          change(value, date, endDate) {
            if (month !== value.split("-")[1]) {
              month = value.split("-")[1];
              console.log("点击了月份");
            }
            self.pageindex = 1;
            self.beginDates = `${value} 00:00:00`;
            self.endDates = `${value} 23:59:59`;
            self.getDeliList(self._param, self.pageindex);
            self.caleCalss();
            let startdate = laydate.getEndDate(10);
            let enddate = laydate.getEndDate();
          }
        });
      }, 0);
    },
    async getDates() {
      this.param.qType = this.dayE;
      let a = await this.getList(this.param);
      let marks = {};
      this.fcEvent.forEach(val => {
        marks[val] = "";
      });
      this.marks = marks;
    },
    caleCalss() {
      setTimeout(() => {
        let box1 = document.getElementsByClassName("layui-laydate");
        box1[0].style.width = "100%";
        let box2 = document.getElementsByClassName("layui-laydate-main");
        box2[0].style.width = "100%";
        let box3 = document.getElementsByClassName("layui-laydate-content");
        let box4 = box3[0].getElementsByTagName("table")[0];
        box4.style.width = "100%";
        let box5 = document.getElementsByClassName("laydate-day-mark3");
        for (let i = 0; i < Array.from(box5).length; i++) {
          box5[i].style.background = this.newTasStateBack;
        }
      }, 0);
    },
    putworkSta(item, txt) {
      let state = dayuan.putWorkState(txt);
      this.putParam = {
        id: item.ID,
        taskState: state,
        taskResult: ""
      };
      // state 修改任务状态  1为未完成 4为取消任务 3为已完成 6转其他人
      if (state === 1 || state === 4) {
        this.compShow = true;
        //点击确定调用getTxtval
      } else if (state === 3) {
        if (item.TaskType === 2) {
          // 普通任务已完成
          this.compShow = true;
        } else {
          // 客户任务已完成
          this.$router.push({
            path: "/addFollow",
            query: {
              cId: item.CustomerID,
              cName: item.CustomerName,
              cLink: item.LinkUserID
            }
          });
        }
      } else {
        this.transferWork(item.ID);
      }
    },
    getTxtval(val) {
      this.putParam.taskResult = val;
      this.cancelWork(this.putParam);
    },
    async cancelWork(param) {
      let put = await this.putWorkMeth(param); //先修改状态
      this.getDates();
    },
    putWorkMeth(param) {
      return this.Axios.post(
        `/CrmTask/SetCrmTaskState/${param.id}?taskState=${
          param.taskState
        }&taskResult=${param.taskResult}`
      ).then(res => {
        this.$vux.toast.show({
          text: "成功修改任务状态"
        });
      });
    },
    getWorkInfo(id) {
      return this.Axios.get(`/CrmTask/GetCrmTaskInfoByID/${id}`)
        .then(res => {
          return res.data.Data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    async transferWork(id) {
      let info = await this.getWorkInfo(id); //先获取任务详情
      this.workInfo = {};
      this.defaultKey.forEach(key => {
        this.workInfo[key] = info[key];
      });
      this.workInfo["Senders"] = info["Senders"].map(val => {
        return {
          UserID: val.UserID,
          IsDelete: 0,
          ID: val.ID
        };
      });
      this.defaultExt = info["Executors"].map(val => {
        return {
          UserID: val.UserID,
          IsDelete: 1,
          ID: val.ID
        };
      });
      this.userBool = true;
    },
    async saveUser(newUser) {
      if (newUser.length) {
        this.userBool = false;
        let c = newUser.map(val => {
          return {
            IsDelete: 0,
            ID: 0,
            UserID: val.ID
          };
        });
        this.workInfo["Executors"] = [...c, ...this.defaultExt];
        let savTas = await this.saveTask();
        // this.$refs.usersel.checkList = [];
        this.getDates();
        this.$vux.toast.show({
          text: "转交成功"
        });
      } else {
        this.$vux.toast.show({
          type: "warn",
          text: "请选择新的执行人"
        });
      }
    },
    saveTask() {
      return this.Axios.post(`/CrmTask/SaveCrmTask`, this.workInfo).then(
        res => {}
      );
    },
    addFlMeth() {
      // 客户任务回调
      return new Promise((resolve, reject) => {
        this.$bus.on("onSave", data => {
          this.cancelWork(this.putParam);
          this.$bus.off("onSave");
        });
      });
    }
  }
};



