import TabItem from 'vux/src/components/tab/tab-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import dayjs from "dayjs";
import List from "./List/template";
import dayuan from "../../common/js/dayuan.js";
import CalendarModel from "./Calendar/template";

import FilterSel from "../../components/filter";
export default {
data() {
return {
tabText: ["我执行的", "我创建的", "抄送给我的"],
tabTextIndex: 0,
doWorkTab: ["进行中", "指定时间", "任务类型", "列表模式"],
param: {
workState: "进行中",
beginDate: "",
endDate: "",
workType: "全部"
},
workStaateSelList: ["进行中", "已完成", "未完成", "已取消", "已超期"],
workStaateSelShow: false,
timeSelList: ["今天", "明天", "后天", "一周内", "所有时间"],
timeSelShow: false,
worktypeSelList: ["全部", "客户跟进任务", "普通任务"],
worktypeSelShow: false,
modelSelList: ["列表模式", "日历模式"],
modelSelShow: false,
filterArr: [
{
type: 1,
init: 0,
name: "选择任务状态",
notDict: true, //不是字典传true
list: [
{
types: "选择任务状态",
ID: 0,
name: "进行中"
},
{
types: "选择任务状态",
ID: 1,
name: "已完成"
},
{
types: "选择任务状态",
ID: 2,
name: "未完成"
},
{
types: "选择任务状态",
ID: 3,
name: "已取消"
},
{
types: "选择任务状态",
ID: 4,
name: "已超期"
}
]
},
{
type: 2,
name: "任务类型",
init: 0,
notDict: true, //不是字典传true
list: [
{
types: "任务类型",
ID: 0,
name: "全部"
},
{
types: "任务类型",
ID: 1,
name: "客户跟进任务"
},
{
types: "任务类型",
ID: 2,
name: "普通任务"
}
]
},
{
type: 3,
init: 0,
name: "显示模式",
notDict: true, //不是字典传true
list: [
{
types: "显示模式",
ID: 0,
name: "列表模式"
},
{
types: "显示模式",
ID: 1,
name: "日历模式"
}
]
}
],
rl: "列表模式"
};
},
computed: {
defaultParam() {
let param = {
taskState: dayuan.workState(this.param.workState),
beginDate: this.param.beginDate,
endDate: this.param.endDate,
taskType: dayuan.workType(this.param.workType)
};
return param;
}
},
directives: {
TransferDom
},
components: {
XHeader,
Tab,
TabItem,
XDialog,
Radio,
Group,
Calendar,
XButton,
List,
CalendarModel,
FilterSel

},
methods: {
hello(i) {
setTimeout(() => {
if (i.types === "选择任务状态") {
this.workStateSelect(i.name);
}
if (i.types === "任务类型") {
this.worktypeSelect(i.name);
}
if (i.types === "显示模式") {
this.rl = i.name;
}
});
},
clearFilter() {
console.log(1);
this.param = {
workState: "进行中",
beginDate: "",
endDate: "",
workType: "全部"
};
this.tabTextIndex = 0;
this.rl = "列表模式";
this.clearInit();
this.$refs["filterSele"].clearClass(this.filterArr);
},
toTime(date) {
setTimeout(() => {
let times = date["beginDate"].replace(/-/g, "/");
let tis = new Date(times).getTime();
let timee = date["endDate"].replace(/-/g, "/");
let tie = new Date(timee).getTime();
if (tis > tie) {
this.$vux.toast.show({
type: "warn",
text: "开始时间不能大于截止时间"
});
return;
}
this.param.beginDate = `${date["beginDate"]}:00`;
this.param.endDate = `${date["endDate"]}:00`;
});
},
tabLeft() {},
tabRight() {
this.$router.push({ path: "/AddTask" });
},
tabClick(index) {
setTimeout(() => {
this.tabTextIndex = index;
});
},
doworkTabClick(index) {

switch (index) {
case 0:
this.workStaateSelShow = true;
break;
case 1:
this.timeSelShow = true;
break;
case 2:
this.worktypeSelShow = true;
break;
case 3:
this.modelSelShow = true;
break;
}
},
workStateSelect(value) {
this.workStaateSelShow = false;
this.doWorkTab[0] = value;
this.param.workState = value;
},
timeSelect(value) {
this.timeSelShow = false;
this.doWorkTab[1] = value;
this.param.day = value;
},
worktypeSelect(value) {
this.worktypeSelShow = false;
this.doWorkTab[2] = value;
this.param.workType = value;
},
modelSelect(value) {
this.modelSelShow = false;
this.doWorkTab[3] = value;
},
clearInit() {
this.filterArr = [
{
type: 1,
init: 0,
name: "选择任务状态",
notDict: true, //不是字典传true
list: [
{
types: "选择任务状态",
ID: 0,
name: "进行中"
},
{
types: "选择任务状态",
ID: 1,
name: "已完成"
},
{
types: "选择任务状态",
ID: 2,
name: "未完成"
},
{
types: "选择任务状态",
ID: 3,
name: "已取消"
},
{
types: "选择任务状态",
ID: 4,
name: "已超期"
}
]
},
{
type: 2,
name: "任务类型",
init: 0,
notDict: true, //不是字典传true
list: [
{
types: "任务类型",
ID: 0,
name: "全部"
},
{
types: "任务类型",
ID: 1,
name: "客户跟进任务"
},
{
types: "任务类型",
ID: 2,
name: "普通任务"
}
]
},
{
type: 3,
init: 0,
name: "显示模式",
notDict: true, //不是字典传true
list: [
{
types: "显示模式",
ID: 0,
name: "列表模式"
},
{
types: "显示模式",
ID: 1,
name: "日历模式"
}
]
}
];
}







},
watch: {
$route(to, form) {
if (to.path === "/" || to.path === "/tipIndex") {
this.clearFilter();
}
}
}
};



