import dayjs from "dayjs";
import noData from "../../../components/noData";
import scroll from "../../../components/scrollAll";
import dayuan from "../../../common/js/dayuan.js";
import yiwc from "../../../assets/yiwc.png";
import weiwc from "../../../assets/weiwc.png";
import yiqx from "../../../assets/qux.png";
import zhuanj from "../../../assets/zhuanj.png";
export default {
  data() {
    return {
      pullUp: {
        threshold: 0,
        txt: {
          more: "加载更多",
          noMore: "没有更多了"
        }
      },
      pullDown: {
        threshold: 50,
        stop: 20
      },
      putWoText: ["已完成", "未完成", "取消任务", "转其他人"],
      iconsArr: [yiwc, weiwc, yiqx, zhuanj]
    };
  },
  computed: {
    aImg() {
      let arr = this.workList.map(val => {
        return dayuan.aImg(val.CreateUserID);
      });
      return arr;
    },
    createTime() {
      let timeArr = this.workList.map(val =>
        dayjs(val.BeginDate).format("YYYY-MM-DD HH:mm")
      );
      return timeArr;
    },
    extName() {
      let name = this.workList.map(val => val.Executors);
      return name;
    },
    exeImg() {
      let exelist = this.workList.map(val => val.Executors);
      let arr = [];
      exelist.forEach((item, key) => {
        if (!arr[key]) {
          arr[key] = [];
        }
        item.forEach(items => {
          arr[key].push(dayuan.aImg(items.UserID));
        });
      });
      return arr;
    },
    neWoSt() {
      return dayuan.newTasState(this.states);
    },
    neWoClass() {
      return dayuan.newTasStateClass(this.states);
    },
    aImg2() {
      let arr = this.workList.map(val => {
        return dayuan.aImg2(val.CreateUserID);
      });
      return arr;
    }
  },
  props: {
    workList: {
      type: Array
    },
    ovoState: {
      type: Number
    },
    param: {
      type: Object
    },
    states: {
      type: Number
    }
  },
  watch: {
    ovoState() {
      this.$refs["scroll"].scrollTo(0, 0, 200);
    },
    param() {
      this.$refs["scroll"].scrollTo(0, 0, 200);
    }
  },
  methods: {
    listPullup() {
      this.$emit("listPullup");
    },
    listPulldown() {
      this.$emit("listPulldown");
    },
    init() {
      let hei = document.body.clientHeight;
      this.$refs.clientList.style.height = `${hei - 365}px`;
    },
    toTask(id) {
      this.$router.push({ path: "/TaskInfo", query: { id } });
    },
    putworkSta(item, txt) {
      this.$emit("putworkSta", item, txt);
    }
  },
  mounted() {
    setTimeout(() => {
      this.init();
    }, 0);
  },
  components: {
    noData,
    scroll
  }
};



